export async function PromiseChain(listado, createPromise) {
    const salida = [];
    for (let actual of listado) {
        const resultado = await createPromise(actual);
        salida.push(resultado);
    }
    return salida;
}
export async function PromiseChainWTB(listado, delay = 0, threads = 1) {
    const salida = [];
    const items = [];
    for (let i = 0; i < threads; i++) {
        const item = listado.shift();
        if (item) {
            items.push(item);
        }
    }
    if (items.length) {
        const itemResults = await Promise.all(items.map(item => item()));
        if (delay > 0) {
            await PromiseDelayed(delay);
        }
        salida.push(...itemResults);
        return salida.concat(await PromiseChainWTB(listado, delay, threads));
    }
    return salida;
}
export class PromiseTimeoutError extends Error {
    ms;
    constructor(ms) {
        super("Timed out");
        this.ms = ms;
    }
}
export async function PromiseTimeout(promesa, ms) {
    return await Promise.race([
        promesa,
        PromiseDelayed(ms).then(() => Promise.reject(new PromiseTimeoutError(ms))),
    ]);
}
export async function PromiseAny(promesas) {
    return await Promise.all(promesas.map(promesa => PromiseResult(promesa)));
}
export async function PromiseResult(promesa) {
    return promesa.then((resultado) => {
        return {
            status: "OK",
            result: resultado,
        };
    }).catch((e) => {
        return {
            status: "KO",
            error: e,
        };
    });
}
export function PromiseMap(array, createPromise) {
    const promesas = [];
    for (const item of array) {
        promesas.push(createPromise(item));
    }
    return promesas;
}
